import React from 'react';
// import ResourceStore from '../../stores/resourceStore.js'
import TechItem from './techItem'
import techTree from '../../utils/tech.json'

let nodeRender = true;
let refs = {};
let renderedNodes;
let numberOfNodes = 0;
let sortedTree = [];
let refCount = 0;
export default class TechTree extends React.Component {

    // componentWillMount() {
    //     this.setState(this.getStateFromStores());
    // }

    // componentDidMount() {
    //     ResourceStore.addChangeListener(this.onChange);
    // }

    // getStateFromStores = () => {
    //     return {
    //         resources: ResourceStore.getAll(),
    //     };
    // }

    // onChange = () => {
    //     this.setState(this.getStateFromStores());
    // }
    constructor(props) {
        super(props)
        
        this.state = {
            nodes: []
        }
    }

    componentDidMount() {
        if (nodeRender) {
            this.setState({nodes: renderedNodes})
        }
    }

    createLines(index) {
        if (Object.keys(refs).length > 0) {
            let lines = [];

            for (var key in sortedTree[index + 1]) {
                const parentTech = techTree[key].parentTech[0]

                lines.push(
                    <line 
                        x1={refs[parentTech].offsetLeft + refs[parentTech].clientWidth/2} 
                        y1={0} 
                        x2={refs[key].offsetLeft + refs[key].clientWidth/2} 
                        y2='100%'
                        strokeWidth="2"
                        stroke="white"
                        key={key}
                    />
                )
            }
                
            return (
                <div className='LineDiv'>
                    <svg width="100%" height='30px'>
                        {lines}
                    </svg>
                </div>
            )
        }
    }

    addRefs = (ref) => {
        refCount++;
        Object.defineProperty(refs, ref.id, {value: ref, writeable: true, enumerable: true, configurable: true})
        if (refCount == numberOfNodes) {
            this.addBranchLines();
        }
    }

    addBranchLines = () => {
        const numBranchLines = this.state.nodes.length - 1;
        let newTree = this.state.nodes.slice();
        for (var i = 0; i < numBranchLines; i++) {
            let newIndex = i * 2 + 1;
            newTree.splice(newIndex, 0, (
                <div className='row center'  key={"lines" + i}>
                    {this.createLines(i)}
                </div>
            ))
        }
        this.setState({nodes: newTree})
    }

    createTree = () => {
        sortedTree = this.groupByRow();
        let tree = Object.keys(sortedTree).map((key) => {
            return (
                <div className="TreeRow row" key={key} >
                    {this.createRow(sortedTree[key])}
                </div>
            )
        })

        numberOfNodes = this.countNodes();
        renderedNodes = tree;
        // return tree;
    }

    countNodes = () => {
        return Object.keys(techTree).length;
    }

    createRow = (technologies) => {
        refs = [];
        let row = Object.keys(technologies).map((key) => {
            return (
                <div key={key} id={key} ref={this.addRefs}>
                    <TechItem
                        tech={technologies[key]}
                        name={key}
                        />
                </div>
            )
        })
        return row
    }

    groupByParent() {
        let techSorted = {};

        Object.keys(techTree).map(key => {
            for (var i in techTree[key].parentTech) {
                if (!techSorted[techTree[key].parentTech[i]]) {
                    techSorted[techTree[key].parentTech[i]] = {};
                }
                techSorted[techTree[key].parentTech[i]][key] = techTree[key];
            }
        })
        return techSorted
    }

    groupByRow(techSorted = [{"Gathering": techTree[Object.keys(techTree)[0]]}], techsLeft = this.removeElement(Object.keys(techTree), 'Gathering'), index = 0) {
        let techsToBeDeleted = [];
        techSorted.push([]); // add the next row;
        
        for (var key in techsLeft) { //look at all techs that have yet to be put in the tree structure
            const parents = techTree[techsLeft[key]].parentTech
            for (var i in parents) {
                if (Object.keys(techSorted[index]).includes(parents[i])){ //If the parent exists in the previous set of nodes (row) then put it in this set.
                    Object.defineProperty(techSorted[index+1], techsLeft[key], { value: techTree[techsLeft[key]], writeable: true, enumerable: true, configurable: true})
                    // techSorted[index+1].push(techTree[techsLeft[key]])
                    techsToBeDeleted.push(techsLeft[key])
                }
            }
        }

        if(techsLeft.length < 1) {
            // techSorted = techSorted.splice(techSorted.length -1, 1);
            return techSorted
        }

        for(var deletedTech in techsToBeDeleted) {
            techsLeft = this.removeElement(techsLeft, techsToBeDeleted[deletedTech]);
        }
        index = index + 1;
        return this.groupByRow(techSorted, techsLeft, index);
    }

    removeElement(array, element) {
        return array.filter(e => e !== element);
    }

    render() {
        if (nodeRender) {
            // nodeRender = false;
            refCount = 0;
            this.createTree();
            return (
                <div>
                    {this.state.nodes}
                </div>
            );
        }
        else {
            nodeRender = true;
            return (
                <div>
                    {this.addBranchLines()}
                </div>
            );
        }
    }
}


