import AppDispatcher from '../dispatcher/dispatcher';
import EventEmitter from 'events';
import Population from '../utils/population.json'

const CHANGE_EVENT = 'change';

let _nodes = Population;
for(var i in Population) {
    _nodes[i] = parseInt(Population[i], 10);
}

class PopulationStoreClass extends EventEmitter {

    emitChange() {
        this.emit(CHANGE_EVENT);
    }

    emitError() {
        this.emit('no_idle');
    }

    addChangeListener(callback) {
        this.on(CHANGE_EVENT, callback);
    }

    get(id) {
        return _nodes[id];
    }

    getAll() {
        return _nodes;
    }
};

let PopulationStore = new PopulationStoreClass();

PopulationStore.dispatchToken = AppDispatcher.register(function(payload) {
    const action = payload.action;
    switch(action.type) {
        case "UPDATE_POPULATION":
            if(_nodes.Idle > 0) {
                _nodes.Idle--;
                _nodes[action.job]++;
                PopulationStore.emitChange();
            } else {
                PopulationStore.emitError();
            }
            break;

        case "REMOVE_POPULATION": {
            if (parseInt(_nodes[action.job]) > 0){
                _nodes.Idle++;
                _nodes[action.job]--;
                PopulationStore.emitChange();
            }
            break;
        }

        case "CREATE_POPULATION":
            _nodes.Idle++;
            PopulationStore.emitChange();
            break;

        default:
        // do nothing
    }
});



export default PopulationStore


